@media screen and (max-width: 1024px) {
    header {
        .nav-link {
            padding-right: 0;
            padding-left: 6px !important;
            margin-top: 18px;
        }

        button {
            background-color: #bdbdbd !important;
            border: 2px solid #333333 !important;

            span {
                background-color:#bdbdbd;
                border-radius: 5px;
                width: 1em !important;
                height: 1em !important;
            }
        }


    }

    .first-screen {
        .row {
            display: flex;
            flex-direction: column;

            .col-md-4 {
                width: 100%;
                text-align: center;
                height: 90vh;
            }

            .col-md-8 {
                width: 100%;
            }
        }
    }

    .about-us {
        .about-block {
            height: 500px;

        }

        .col-md-4 {
            display: none;
        }

        .col-md-3 {
            padding-left: 20px;
            width: 30%;
        }

        .col-md-5 {
            padding-left: 70px;
            width: 70%;

            h2 {
                font-size: 36px;
            }

            button {
                border: 2px solid #333333;
            }
        }

    }

    .projects {
        height: 170vh;

        .row {
            display: flex;
            flex-direction: column;

        }
    }

    .contact {
        height: 70vh;
    }

    .main-task {
        height: 70vh;
    }

    footer {
        .row {
            .logo-block {
                width: 30%;
            }

            .info-block {
                display: none;
            }

            .contacts-block {
                width: 30%;
            }

            .social-block {
                width: 30%;
            }
        }
    }
}
