.projects{
    height: 90vh;
    margin-top: 50px;
    h2{
        
        line-height: 64px;
        font-weight: 300;
        color: #bdbdbd;
        margin-bottom: 70px;
    }
    .col-md-6{   
        margin-bottom: 25px;
        .gray-block{
            position: relative;
            width: 570px;
            height: 100%;
            background-color: #333333;
            img{
                opacity: 0.3;
            }
            .text-block{
                position: absolute;
                top: 35px;
                left: 80px;
                h2{
                    font-weight: 700;
                    line-height: 64px;
                    color: #fff;
                    margin-bottom: 13px;
                    margin-top: 10px;
                }
                a{
                    color: #fff;
                    text-decoration: none;
                    letter-spacing: 2px;
                    font-size: 12px;
                    &:hover{
                        font-weight: 600;
                    }
                    img{
                        margin-left: 10px;
                        margin-bottom: 2px;
                        opacity: 1;
                    }
                }
            }
        }
    }
    .col-md-5{
        padding-left: 115px;
       
    }

    button{
        margin-top: 35px;
        background-color: #333333;
        color: #fff;
        margin-right: -10px;
        width: 222px;
        height: 71px;
        float: right;
        letter-spacing: 3px;
        font-size: 12px;
        &:hover{
            background-color: #4b4b4b;
        }
        img{
            margin-left: 10px;
        }
    }
}