@media screen and (max-width: 1199px) {
    .about-us{
        .about-block{
            height: 520px;
            
        }
        .col-md-4{
            display: none;
        }
        .col-md-3{
            padding-left: 30px;
            width: 50%;
        }
        .col-md-5{
            padding-left: 60px;
            width: 50%;
            h2{
                font-size: 36px;
            }
            button{
                border: 2px solid #333333;
            }
        }
        
    }
    .projects{
        height: 170vh;
        .row{
            display: flex;
            flex-direction: column;
        }
        button{
            margin-right: 0;
        }
    }
    .contact{
        height: 70vh;
    }
    .main-task{
        height: 50vh;
    }
}
