.about-us {
    height: 60vh;
    margin-top: 120px;

    .about-block {
        background-color: #FBFBFB;
        height: 435px;

        .col-md-4 {
            
            padding-top: 30px;
            padding-left: 100px;
            img{
                margin-bottom: 30px;
            }
        }

        .col-md-3 {
            
            padding-top: 60px;
        }

        .col-md-5 {
            
            padding-right: 120px;
            padding-top: 30px;
            h2{
                
                line-height: 64px;
                color: #bdbdbd;
                font-weight: 300;
                margin-bottom: 24px;
            }
            p{
                margin-bottom: 25px;
            }
            button{
                background-color: #fff;
                border: none;
                font-size: 12px;
                font-weight: 400;
                letter-spacing: 3px;
                width: 222px;
                height: 71px;
                img{
                    margin-left: 5px;
                    margin-bottom: 2px;
                }
            }
        }
    }
}
