footer{
    height: 417px;
    background-color: #333333;
    margin-top: auto;
    padding-top: 80px;
    .row{
        .logo-block{
            width: 20%;
           
        }
        .info-block{
            width: 20%;
            color: #fff;
            h5{
                margin-bottom: 25px;
                font-size: 16px;
                font-weight: 600;
                line-height: 20px;
            }

        }
        .contacts-block{
            width: 30%;
            color: #fff;
            h5{
                margin-bottom: 25px;
                font-weight: 600;
                line-height: 20px;
            }
            
            .geo{
                display: flex;
                margin-bottom: 30px;
                position: relative;
                &-text{
                    width: 250px;
                    line-height: 24px;
                    font-size: 14px;
                    font-weight: 500;
                    .mail{
                        margin-top: -6px;
                    }
                }
                img{
                    margin-right: 15px;
                    height: 100%;
                }

            }
        }
        .social-block{
            width: 30%;
            color: #fff;
            h5{
                margin-bottom: 25px;
                font-size: 16px;
                font-weight: 600;
                line-height: 20px;
            }
            .social-box{
                display: flex;
                img{
                    margin-right: 50px;
                }
            }
        }
    }
}

 