@media screen and (max-width: 767px) {
    .about-us{
        height: 95vh;
        .about-block{
            height: 900px;
            
        }
        .col-md-4{
            display: none;
        }
        .col-md-3{

            width: 30%;
        }
        .col-md-5{
            width: 100%;
            h2{
                font-size: 36px;
            }
            button{
                border: 2px solid #333333;
            }
        }
        
    }
    .projects{
        height: 170vh;
        .row{
            display: flex;
            flex-direction: column;
            .col-md-3{
                margin-bottom: 30px;
            }
            .col-md-6{
                .second-img{
                    width: 500px;
                }
                .gray-block{
                    width: 500px;
                    h2{
                        font-size: 36px;
                    } 
                }
        }
    }
}
    .contact{
        height: 100vh;
    }
    .main-task{
        height: 85vh;
    }

    footer{
        .row{
            .logo-block{
                width: 50%;
            }
            .info-block{
                display: none;
            }  
            .contacts-block{
                width: 50%;
            }
            .social-block{
                display: none;
            }
        }
    }

}
