@media screen and (max-width: 575px) {
    .about-us {
        height: 85vh;

        .about-block {
            height: 800px;

        }

        .col-md-3 {
           display: none;
        }

        .col-md-5 {
            width: 100%;

            h2 {
                font-size: 28px;
            }

            button {
                border: 2px solid #333333;
            }
        }

    }

    .projects{
        height: 110vh;
        .row{
            display: flex;
            flex-direction: column;
            .col-md-5{
                display: none;
            }
            .col-md-3{
                .third-img{
                    width: 320px;
                }
            }
            .col-md-6{
                .second-img{
                    width: 320px;
                }
                .gray-block{
                    width: 320px;
                    h2{
                        font-size: 22px;
                        margin-top: 0;
                    } 
                }
        }
        .col-md-4{
            display: none;
        }
    }
}

    .contact {
        height: 100vh;
        .p-box{
            width: 300px;

        }
    }
    .main-task{
        height: 85vh;
        p{
            font-size: 12px;
        }
    }

    footer{
        .row{
            .logo-block{
                display: none;
            }
            .info-block{
                display: none;
            }  
            .contacts-block{
                width: 100%;
            }
            .social-block{
                display: none;
            }
        }
    }
}
