header {
    .navbar-collapse {
        justify-content: end;
        margin-right: 60px;

        .nav-link {
            margin-right: 47px;
            color: #333333;
            font-size: 12px;
            line-height: 12px;
            letter-spacing: 2px;
        }
    }

    height: 102px;
    padding: 15px 0;
    background: #fff;
}

#lined {
    position: relative;

    &::before {
        content: "";
        position: absolute;
        width: 65px;
        height: 1px;
        background-color: #000;
        top: 3px;
        left: 8px;
    }

    &::after {
        content: "";
        position: absolute;
        width: 65px;
        height: 1px;
        background-color: #000;
        bottom: 3px;
        left: 8px;
    }
}
