.main-task {
    height: 38vh;

    .title-task {
        h2 {
            
            line-height: 64px;
            font-weight: 300;
            color: #bdbdbd;
        }

        margin-bottom: 25px;
    }

    .col-md-6 {
        
        display: flex;

        span {
            font-size: 190px;
            line-height: 238px;
            font-weight: 900;
            color: #F2F2F2;
            font-family: sans-serif;
            
        }

        .first-task {
            width: 300px;
            padding-top: 55px;
            padding-left: 10px;

            p {
                font-weight: 400;
                font-size: 22px;
                line-height: 36px;
                color: #333333;
            }
        }

        .second-task {
            width: 460px;
            padding-top: 53px;
            padding-left: 16px;

            p {
                font-weight: 400;
                font-size: 22px;
                line-height: 36px;
                color: #333333;
            }
        }

    }
}
