.first-screen{
    .col-md-4{
        padding-top: 275px;
        p{
            font-size: 64px;
            font-weight: 300;
            margin-bottom: -25px;
        }
        span{
            font-size: 64px;
            font-weight: 700;
        } 
        .arrow-right{
            margin-top: 90px;
            height: 53px;
            width: 53px;
            margin-right: 15px;
            margin-bottom: 90px;
            border: 2px solid #F2F2F2;
        }
        .arrow-left{
            margin-top: 90px;
            height: 53px;
            width: 53px;
            margin-right: 15px;
            margin-bottom: 90px;
            border: 2px solid #F2F2F2;
            background-color: #fff;
        }

        .data{
            height: 31px;
            border: none;
            span{
                padding-right: 20px;
                color: #BDBDBD;
                font-size: 24px;
                font-weight: 400;
                line-height: 24px;
            }
            img{
                margin-bottom: 4px;
            }
            .after-slash{
                margin-left: 20px;
            }
        }
    }
    .col-md-8{
        position: relative;
        button{
            position: absolute;
            bottom: 0px;
            width: 222px;
            height: 71px;
            left: 12px;
            background-color: #fff;
            border: none;
            font-size: 12px;
            font-weight: 400;
            letter-spacing: 2px;
            img{
                margin-left: 10px;
            }
        }
    }
    

}