.contact{
    height: 75vh;
    .title-contact{
        h2{
            
            line-height: 64px;
            font-weight: 300;
            color: #bdbdbd;
            margin-bottom: 25px;
        }
    }
    .col-md-4{

        .input-box {
            position: relative;
            width: 100%;
            margin: 9px;
            input[type="text"] {
                width: 100%;
                padding: 10px;
                padding-left: 15px;
                background-color: #F3F3F3;
                border: none;
              }
          }
        
          .star-phone {
            position: absolute;
            top: 5px;
            left: 140px;
            color: red;
            pointer-events: none;
          }
          .star-mail {
            position: absolute;
            top: 5px;
            left: 62px;
            color: red;
            pointer-events: none;
          }
          .star-message {
            position: absolute;
            top: 5px;
            left: 105px;
            color: red;
            pointer-events: none;
          }
    }
    .col-md-8{
        
    }

    .p-box{
        width: 391px;
        font-size: 14px;
        font-weight: 400;
        line-height: 17px;
        padding-top: 15px;
        padding-left: 45px;
    }
    button{
        background-color: #333333;
        color: #fff;
        letter-spacing: 3px;
        height: 71px;
        width: 222px;
        margin: 12px;
        font-size: 12px;
        img{
            margin-left: 10px;
        }
    }
}
#message{
    height: 147px;
    padding-bottom: 120px;
}